import store from '@/store'

// 
export const reminderUtils = {
    // ID
    TEMPLATE_IDS: {
        GOAL_REMINDER: 'W4_lajK--Gg10QEqoCaA7QFyCxcbHVw5s0jkzzAKqL4', // 
    },

    // ID
    checkTimer: null,

    // 
    startChecking() {
        console.log('...')
        // 
        if (this.checkTimer) {
            console.log('')
            clearInterval(this.checkTimer)
            this.checkTimer = null
        }

        // 
        const token = store.state.user.token
        console.log('token:', token ? '' : '')
        if (!token) {
            console.log('')
            return
        }

        // store
        const userSettings = store.state.settings.settings
        console.log('store:', userSettings)
        
        if (!userSettings?.enableReminder) {
            console.log('')
            return
        }

        // 
        let nextReminderTime = uni.getStorageSync('nextReminderTime')
        
        // 
        if (!nextReminderTime) {
            const reminderTime = userSettings.reminderTime
            if (reminderTime) {
                const [hours, minutes] = reminderTime.split(':').map(Number)
                const next = new Date()
                next.setHours(hours, minutes, 0, 0)
                
                // 
                if (next.getTime() <= Date.now()) {
                    next.setDate(next.getDate() + 1)
                }
                
                console.log(':', next.toLocaleString())
                nextReminderTime = next.getTime()
                uni.setStorageSync('nextReminderTime', nextReminderTime)
            }
        } else {
            console.log(':', new Date(nextReminderTime).toLocaleString())
        }

        //  - 
        this.checkTimer = setInterval(() => {
            console.log('...')
            this.checkReminder()
        }, 60000) // 1
        
        // 
        console.log('')
        this.checkReminder()
    },

    // 
    stopChecking() {
        console.log('')
        if (this.checkTimer) {
            clearInterval(this.checkTimer)
            this.checkTimer = null
        }
        // 
    },

    // 
    async setReminder(time, data) {
        try {
            console.log(':', time)
            const tmplData = {
                thing1: { value: `:${data.targetWeight}kg/${data.targetBMI}BMI` },
                thing2: { value: `${data.targetSteps}/` },
                thing3: { value: `${data.targetDuration}/` }
            }

            // 
            const [hours, minutes] = time.split(':').map(Number)
            const now = new Date()
            const reminderTime = new Date()
            reminderTime.setHours(hours, minutes, 0, 0)

            // 
            if (now.getTime() > reminderTime.getTime()) {
                reminderTime.setDate(reminderTime.getDate() + 1)
            }
            
            console.log(':', reminderTime.toLocaleString())

            // 
            uni.setStorageSync('nextReminderTime', reminderTime.getTime())
            uni.setStorageSync('reminderTime', time)
            uni.setStorageSync('reminderData', tmplData)
            
            // 
            uni.removeStorageSync('lastRemindDate')
            
            // 
            this.startChecking()

            return true
        } catch (error) {
            console.error(':', error)
            return false
        }
    },
    
    // 
    cancelReminder() {
        console.log('')
        uni.removeStorageSync('nextReminderTime')
        uni.removeStorageSync('reminderTime')
        uni.removeStorageSync('reminderData')
        uni.removeStorageSync('lastRemindDate')
    },
    
    // 
    async checkReminder() {
        try {
            // 
            const token = store.state.user.token
            if (!token) {
                console.log('')
                this.stopChecking()
                return
            }

            // 
            const userSettings = store.state.settings.settings
            if (!userSettings?.enableReminder) {
                console.log('')
                return
            }

            const nextReminderTime = uni.getStorageSync('nextReminderTime')
            if (!nextReminderTime) {
                console.log('')
                return
            }

            const now = Date.now()
            console.log(':', new Date(now).toLocaleString())
            console.log(':', new Date(nextReminderTime).toLocaleString())
            
            // 
            const lastRemindDate = uni.getStorageSync('lastRemindDate')
            const today = new Date().toDateString()
            
            // 
            if (lastRemindDate === today) {
                console.log('')
                return
            }

            // 
            if (now >= nextReminderTime) {
                console.log('')
                const reminderData = uni.getStorageSync('reminderData')
                
                if (reminderData) {
                    // 
                    await uni.showModal({
                        title: '',
                        content: `${reminderData.thing1.value}\n${reminderData.thing2.value}\n${reminderData.thing3.value}`,
                        showCancel: false
                    })
                    console.log('')
                    
                    // 
                    uni.setStorageSync('lastRemindDate', today)

                    // 
                    const time = uni.getStorageSync('reminderTime')
                    if (time) {
                        const [hours, minutes] = time.split(':').map(Number)
                        const next = new Date()
                        next.setHours(hours, minutes, 0, 0)
                        next.setDate(next.getDate() + 1)  // 
                        console.log(':', next.toLocaleString())
                        uni.setStorageSync('nextReminderTime', next.getTime())
                    }
                }
            } else {
                console.log('')
            }
        } catch (error) {
            console.error(':', error)
        }
    },

    // 
    async sendReminder() {
        const goals = uni.getStorageSync('reminderGoals')
        if (!goals) return

        uni.showNotification({
            title: '',
            content: goals.content,
            data: {
                sportName: goals.sportName,
                tips: goals.tips
            }
        })
    }
} 