import { getUserSettingsAPI, updateSettingsAPI } from '@/api/settings'

export default {
    namespaced: true,
    state: {
        settings: uni.getStorageSync('userSettings') || {
            // 
            targetWeight: '',
            targetBMI: '',
            targetSteps: '',
            targetDuration: '',
            // 
            enableReminder: false,
            reminderTime: '08:00',
            // 
            enableNotification: true,
            enableSync: true,
            // 
            showBMI: true,
            showBloodPressure: true
        }
    },
    mutations: {
        // 
        setSettings(state, settings) {
            state.settings = settings
            // 
            uni.setStorageSync('userSettings', settings)
        }
    },
    actions: {
        // 
        async getSettings({ commit }) {
            try {
                const res = await getUserSettingsAPI()
                if (res.code === 0) {
                    commit('setSettings', res.data)
                    return true
                }
                return false
            } catch (error) {
                console.error(':', error)
                return false
            }
        },
        // 
        async saveSettings({ commit }, settings) {
            try {
                console.log(':', settings)
                const res = await updateSettingsAPI(settings)
                console.log('API:', res)
                
                if (res.code === 0) {
                    commit('setSettings', settings)
                    return true  //  true
                }
                return false
            } catch (error) {
                console.error(':', error)
                return false
            }
        }
    },
    getters: {
        getSettings: state => state.settings
    }
} 